
import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);

//1.
import Guide from "./views/guide.vue";


//2.
const routes = [
    {
        path:"/",
        component:Guide,
        name:guide,
    }
]


//3.
const router = new VueRouter({
    router,
    mode:"hash"
}) 


export default router;



